export default {
	data() {
		return {
			emailNum:'',
			authcode:'',
			opcity:0.5,
			nosendCode: false,
			sendbtn: {
				text: '获取验证码',
				codeTime: 60
			},
		}
	},
	onLoad() {
		
	},
	onReady() {
		
	},
	methods: {
		sendCode(){
			uni.request({
				url: this.baseUrl + "/email-modify-pass",
				data:{
					Email: this.emailNum
				},
				method:"POST",
				// header: {
				// 	//除注册登录外其他的请求都携带用户token和秘钥
				// 	Authorization: uni.getStorageSync('token')
				// },
				success: (res) => {
					console.log(res.data)
					 if (res.data.status == 1) {
						this.sendMsgCodeTimer()
						uni.showToast({
							title:res.data.message
						})
					} else {
						uni.showToast({
							title: res.data.message,
							icon: "none"
						})
					}
				}
			})
		},
		next() {
			uni.navigateTo({
				url:"./resetPassword?emailNum="+this.emailNum+"&authcode"+this.authcode
			})
		},
		change(e){
			console.log(e.detail.value.length)
			if (e.detail.value.length >= 3) {
				this.opcity = 1
			}else{
				this.opcity = 0.5
			}
		},
		
		// 发送短信验证码计时器
		sendMsgCodeTimer() {
			this.timerId = setInterval(() => {
				let codeTime = this.sendbtn.codeTime;
				codeTime--;
				this.sendbtn.codeTime = codeTime;
				this.sendbtn.text = codeTime + "S";
				if (codeTime < 1) {
					clearInterval(this.timerId);
					this.sendbtn.text = "重新获取";
					this.nosendCode = false
					this.sendbtn.codeTime = 60;
				}
			}, 1000);
		},
	}
}
